.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;

    h1 {
      color: white;
      margin: 50px 0;
    }
    header {
      width: 440px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
}
.overlay,
.overlay2 {
  position: absolute;
  width: 100%;
  height: 300px;
  background: rgb(255,192,146);
  background: var(--gradient-primary);
  z-index: -1;
  top: 0;
}
.overlay {
  animation: fadeBg 3s ease 0s infinite alternate;
}
.overlay2 {
  z-index: -2;
  /*background: linear-gradient(90deg, rgba(238,174,202,1) 0%, rgba(148,221,233,1) 100%);*/
  background: var(--gradient-secondary);
}

@keyframes fadeBg {
  0% { opacity: 0 }
  100% { opacity: 1 }
}

.icon {
    width: 32px;
    height: 32px;
    cursor: pointer;
    transition: .5s ease;

      &:hover {
        transform: scale(1.4);
      }
}